
.container {
  height: .64rem;
  box-sizing: border-box;
}
.container .status {
    height: .2rem;
}
.container .content {
    height: .44rem;
}
.container .content .left {
      z-index: 1111;
      position: absolute;
      line-height: .44rem;
      left: .15rem;
      width: .5rem;
      font-size: 12px;
      height: .44rem;
      top: .2rem;
}
.container .content .left i {
        width: .12rem;
        height: .2rem;
        font-size: .16rem;
        position: absolute;
        top: .12rem;
        color: #D5B884;
}
.container .content .center {
      text-align: center;
      padding-left: .8rem;
      padding-right: .8rem;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: .16rem;
      color: #D5B884;
      height: .44rem;
      position: relative;
      line-height: .44rem;
}
.container .content .right {
      z-index: 1111;
      position: absolute;
      right: .15rem;
      font-size: .14rem;
      color: #D5B884;
      text-align: right;
      height: .44rem;
      top: .2rem;
      line-height: .44rem;
}



