
html {
  background: #101010;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
html,
body,
.container {
  color: #b6b6b6;
  font-size: 0.14rem;
}
.van-button {
  border-radius: 1rem;
  background: transparent;
  color: #d5b884;
  border-color: #d5b884;
  width: 2rem;
  height: 0.5rem;
}
.van-field {
  background: transparent;
  border-bottom: 0.5px solid #d5b884;
  padding: 5px 10px 5px 0;
}
.van-field .van-field__control {
    color: #7c7c7c;
    background: transparent;
}
.van-checkbox .van-icon-success {
  border: 1px solid #d5b884;
  border-radius: 0.02rem;
  color: #000;
}
.van-checkbox__control:checked + .van-icon-success {
  border-color: #d5b884;
  background-color: #d5b884;
  color: #0e0e0e;
}
.space {
  height: .2rem;
}



